@import url(http://fonts.googleapis.com/css?family=Roboto:400,100,100italic,300,300ita‌​lic,400italic,500,500italic,700,700italic,900italic,900);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html{
  height: 100%;
  display: table;
  width: 100%;
}
body{
  display: table-cell;
	margin: 0;
	padding: 0;
	background-color: #0e6cc4;
	overflow-x:hidden;
	/* overflow-y:hidden; */
  height: 100%;
  transition: background-color 0.5s;
}
#root{
  height: 100vh;
  width: 100vw;
  display: table-cell;
}
.App{
  height: 100vh;
  width: 100vw;
  display: table-cell;
}
p{
  margin: 0;
}
.alert-card{
  background-color: red !important;
  color: white;
  padding-bottom: 1rem !important;
  padding-top: 1rem !important;
}
.container{
  margin-bottom: 2rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background-color: rgba(255, 255, 255, 90%);
  height: auto;
  padding: 2rem;
  padding-bottom: 1rem;
  border-radius: 20px;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
}

.info-card *{
  margin-bottom: 1rem;
}

.content{
  margin: 5rem;
  width: 40%;
  height: auto;
  display: flex;
  flex-direction: column;
  float: right;
}
.requests{
  margin-top: 2rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background-color: rgba(255, 255, 255, 90%);
  height: auto;
  padding: 2rem;
  border-radius: 20px;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
}
.requests h2{
  margin-bottom: 1rem;
}
.delivery-form{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background-color: rgba(255, 255, 255, 90%);
  height: auto;
  padding: 2rem;
  border-radius: 20px;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
}
.crr-ok{
  color: white;
  font-size: 7vw;
}
.what-link{
  color: white;
  font-size: 2vw;
  text-decoration: none;
}
.text{
  margin: 3rem 5rem 5rem 5rem;
  position: fixed;
}

@media screen and (max-width: 800px) {
  .content{
    width: -webkit-fill-available;
    margin: 0 2rem 2rem 2rem;
  }
  .crr-ok{
    text-align: center;
    font-size: 15vw;
  }
  .text{
    position: relative;
    margin: 2rem;
  }
  .what-link{
    text-align: right;
    font-size: 1.5rem;
    float: inline-end;
    margin-right: 0;
    margin-left: auto;
    /* width: 100%; */
  }
}


/*waves****************************/

.box {
	position: fixed;
	top: 0;
	transform: rotate(80deg);
	left: 0;
  z-index: -1;
}

.wave {
	position: fixed;
	top: 0;
	left: 0;
  opacity: .4;
  position: absolute;
  top: 3%;
  left: 10%;
  background: #0af;
  width: 1500px;
  height: 1300px;
  margin-left: -150px;
  margin-top: -250px;
  transform-origin: 50% 48%;
  border-radius: 43%;
  animation: drift 7000ms infinite linear;
}

.wave.-three {
  animation: drift 7500ms infinite linear;
	position: fixed;
	background-color: #77daff;
}

.wave.-two {
  animation: drift 3000ms infinite linear;
  opacity: .1;
  background: black;
	position: fixed;
}

.box:after {
  content: '';
  display: block;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 11;
  transform: translate3d(0, 0, 0);
}

@keyframes drift {
  from { transform: rotate(0deg); }
  from { transform: rotate(360deg); }
}

/*LOADING SPACE*/

.contain {
	animation-delay: 4s;
	z-index: 1000;
	position: fixed;
	top: 0;
	left: 0;
	bottom: 0;
	right: 0;
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  align-items: center;

  background: #25a7d7;
  background: linear-gradient(#25a7d7, #25a7d7);
}

.icon {
  width: 100px;
  height: 100px;
  margin: 0 5px;
}

/*Animation*/
.icon:nth-child(2) img {animation-delay: 0.2s}
.icon:nth-child(3) img {animation-delay: 0.3s}
.icon:nth-child(4) img {animation-delay: 0.4s}

.icon img {
  animation: anim 2s ease infinite;
  transform: scale(0,0) rotateZ(180deg);
}

@keyframes anim{
  0% {
    transform: scale(0,0) rotateZ(-90deg);opacity:0
  }
  30% {transform: scale(1,1) rotateZ(0deg);opacity:1
  }
  50% {
    transform: scale(1,1) rotateZ(0deg);opacity:1
  }
  80% {
    transform: scale(0,0) rotateZ(90deg);opacity:0
  }
}
